'use client';

import { useState, useEffect } from 'react';
import { DatabaseDebug } from '@/lib/database/debug';

export default function DatabaseStatus() {
  const [stats, setStats] = useState(null);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const loadStats = async () => {
      const result = await DatabaseDebug.getStats();
      if (result.success) {
        setStats(result.stats);
      }
    };
    loadStats();
  }, []);

  const handleClearData = async () => {
    if (confirm('Are you sure you want to clear all local data? This cannot be undone.')) {
      const result = await DatabaseDebug.clearLocalData();
      if (result.success) {
        alert('Local data cleared successfully!');
        window.location.reload();
      } else {
        alert('Error clearing data: ' + result.error);
      }
    }
  };

  const handleReset = async () => {
    if (confirm('Are you sure you want to reset the database? This will clear all local data and reinitialize.')) {
      const result = await DatabaseDebug.reset();
      if (result.success) {
        alert('Database reset successfully!');
        window.location.reload();
      } else {
        alert('Error resetting database: ' + result.error);
      }
    }
  };

  const handleForceReset = async () => {
    if (confirm('Are you sure you want to FORCE reset the database? This will completely delete and recreate the database. This is more aggressive and should fix constraint errors.')) {
      const result = await DatabaseDebug.forceReset();
      if (result.success) {
        alert('Database force reset successfully!');
        window.location.reload();
      } else {
        alert('Error force resetting database: ' + result.error);
      }
    }
  };

  if (!isVisible) {
    return (
      <button
        onClick={() => setIsVisible(true)}
        className="fixed bottom-4 right-4 bg-slate-700 text-white p-2 rounded-full shadow-lg hover:bg-slate-600 transition-colors z-50"
        title="Database Status"
      >
        🗄️
      </button>
    );
  }

  return (
    <div className="fixed bottom-4 right-4 bg-slate-800 border border-slate-600 rounded-lg p-4 shadow-xl z-50 max-w-sm">
      <div className="flex justify-between items-center mb-3">
        <h3 className="text-white font-semibold">Database Status</h3>
        <button
          onClick={() => setIsVisible(false)}
          className="text-slate-400 hover:text-white"
        >
          ✕
        </button>
      </div>
      
      {stats && (
        <div className="space-y-2 text-sm text-slate-300 mb-4">
          <div className="flex justify-between">
            <span>Users:</span>
            <span className="text-cyan-400">{stats.users}</span>
          </div>
          <div className="flex justify-between">
            <span>Patients:</span>
            <span className="text-cyan-400">{stats.patients}</span>
          </div>
          <div className="flex justify-between">
            <span>Sync Queue:</span>
            <span className="text-cyan-400">{stats.syncQueue}</span>
          </div>
          <div className="flex justify-between">
            <span>Status:</span>
            <span className={stats.isOnline ? 'text-green-400' : 'text-yellow-400'}>
              {stats.isOnline ? 'Online' : 'Offline'}
            </span>
          </div>
        </div>
      )}

      <div className="space-y-2">
        <button
          onClick={handleClearData}
          className="w-full bg-red-600 hover:bg-red-700 text-white text-xs py-2 px-3 rounded transition-colors"
        >
          Clear Local Data
        </button>
        <button
          onClick={handleReset}
          className="w-full bg-yellow-600 hover:bg-yellow-700 text-white text-xs py-2 px-3 rounded transition-colors"
        >
          Reset Database
        </button>
      </div>
    </div>
  );
}
